html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

body {
  margin: 0;
  font-family: "GraphikLCG", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 23.2px;
  color: #23272a;
  background-color: #fff;
  overflow: hidden;
}

.promo-statistics-section {
  background-color: #F2F5FF;
  padding-bottom: 100px;
}

.advantages-section {
  padding: 100px 0 0;
}

img {
  display: block;
  max-width: 100%;
  user-select: none;
}

svg {
  display: block;
  height: 100%;
  max-width: 100%;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: #fff;
  transition: opacity 0.3s ease-in-out;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

button {
  margin: 0;
  border: none;
  padding: 0;
  cursor: pointer;
  user-select: none;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.section {
  margin-bottom: 100px;
}

.title {
  font-weight: 700;
  font-size: 36px;
  line-height: 52.2px;
}

.title-secondary {
  font-weight: 700;
  font-size: 52px;
  line-height: 59.8px;
}

.unvisible {
  visibility: hidden !important;
}

.scroll-disabled {
  overflow: hidden !important;
}

input {
  border-radius: 0;
}

.link-button {
  display: inline-block;
  border-radius: 50px;
  padding: 18px 30px;
  font-weight: 500;
  line-height: 16px;
  color: #fff;
  background-color: #5B7EFB;
}

.link-social {
  display: inline-block;
  width: 40px;
  height: 40px;
}
.link-social_telegramm {
  background: url("../img/social-telegramm.svg") center/cover no-repeat;
}
.link-social_wk {
  background: url("../img/social-wk.svg") center/cover no-repeat;
}

.link-underline {
  border-bottom: 1px solid #fff;
}

.swiper-pagination {
  left: 50%;
  bottom: 0px;
  transform: translateX(-50%);
}
.swiper-pagination.promo-slider__pagination {
  bottom: 0px;
}
.swiper-pagination.comments__pagination {
  bottom: -35px;
}
.swiper-pagination .swiper-pagination-bullet {
  transform: scale(1) !important;
  margin: 0 5px !important;
  transition: all 0.3s ease 0s !important;
  width: 6px;
  height: 6px;
  flex: 0 0 6px;
  background-color: rgba(91, 126, 251, 0.5);
}
.swiper-pagination .swiper-pagination-bullet-active {
  transform: scale(2) !important;
  background: #5B7EFB;
}
.swiper-pagination .swiper-pagination-bullet:hover {
  background: rgba(91, 126, 251, 0.8);
}
.swiper-pagination .swiper-pagination-bullet:hover:not(.swiper-pagination-bullet-active) {
  transform: scale(1.5) !important;
}

.vk-link {
  background: url("../img/vk.png") center center no-repeat;
  border-radius: 50%;
  transition: all 0.3s ease 0s;
  width: 50px;
  height: 50px;
}
.vk-link:hover {
  background: url("../img/vk-hover.svg") center center no-repeat;
}

.tg-link {
  background: url("../img/tg-link.svg") center center no-repeat;
  border-radius: 50%;
  transition: all 0.3s ease 0s;
  width: 50px;
  height: 50px;
}
.tg-link:hover {
  background: url("../img/tg-hover.svg") center center no-repeat;
}

.slider-next {
  border: 2px solid #5B7EFB;
  width: 50px;
  height: 50px;
  transition: all 0.3s ease 0s;
  display: inline-flex;
  flex: 0 0 50px;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: url("../img/arrow_right.svg") center center no-repeat;
}
.slider-next:hover {
  background: url("../img/arrow_right_white.svg") center center no-repeat;
  background-color: #5B7EFB;
}

.slider-prev {
  display: inline-flex;
  flex: 0 0 50px;
  align-items: center;
  justify-content: center;
  border: 2px solid #5B7EFB;
  width: 50px;
  height: 50px;
  transition: all 0.3s ease 0s;
  border-radius: 50%;
  background: url("../img/arrow_left.svg") center center no-repeat;
}
.slider-prev:hover {
  background: url("../img/arrow_left_white.svg") center center no-repeat;
