}

.header {
  position: absolute;
  top: 0;
  z-index: 1000;
  padding: 25px 0;
  width: 100%;
  background: transparent;
  transition: all 0.3s ease 0s;
}
.header__logo {
  display: none;
  width: 160px;
  height: 19px;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav .nav-block {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 -15px -10px -15px;
  flex: 0 1 685px;
}
.nav .nav-block__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  color: #FFFFFF;
  transition: all 0.3s ease 0s;
}
.nav .nav-block__link:hover {
  color: #1434A7;
}
.nav .nav-block__item {
  margin: 0 15px 10px 15px;
}
.nav__link_logo {
  width: 160px;
  height: 19px;
  margin-right: 30px;
}
@media (max-width: 1200px) {
  .nav__link_logo {
    margin-right: calc(15px + 15 * ((100vw - 320px) / 880));
  }
}
.nav__link_button {
  padding: 12px 30px;
  border: 2px solid #fff;
  border-radius: 30px;
  white-space: nowrap;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  min-height: 50px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease 0s;
  margin-left: 30px;
}
@media (max-width: 1200px) {
  .nav__link_button {
    margin-left: calc(15px + 15 * ((100vw - 320px) / 880));
  }
}
.nav__link_button:hover {
  background-color: #fff;
  color: #5B7EFB;
  opacity: 1;
}

.header_active {
  position: fixed;
  top: -100%;
  transition: all 0.3s ease 0s !important;
  background: rgba(91, 126, 251, 0.9);
  transform: translateY(-100%) !important;
}

.header_active_middle {
  top: 0 !important;
  transform: translateY(0) !important;
  transition: all 0.3s ease 0s !important;
  padding: 15px 0;
}

.wrapper-nav-block__burger {
  display: none;
  border: 2px solid #fff;
  border-radius: 30px;
  width: 50px;
  height: 50px;
  position: relative;
  z-index: 100;
  margin-right: 20px;
}
@media (max-width: 768px) {
  .wrapper-nav-block__burger {
    display: block;
  }
}

.burger-button {
  position: relative;
  display: none;
  width: 24px;
  height: 18px;
  background-color: transparent;
  z-index: 100;
  margin-top: 14px;
  margin-left: 10px;
}
.burger-button__line {
  position: absolute;
  left: 0;
  display: block;
  border-radius: 10px;
  width: 100%;
  height: 3px;
  background-color: #fff;
  transition: transform 0.3s linear;
}
.burger-button__line_1 {
  top: 0;
}
.burger-button__line_2 {
  top: 50%;
  transform: translateY(-50%);
  width: 77%;
}
.burger-button__line_3 {
  bottom: 0;
}
.burger-button_active .burger-button__line_1 {
  top: 51%;
  transform: rotate(45deg) translateY(-50%);
}
.burger-button_active .burger-button__line_2 {
  opacity: 0;
}
.burger-button_active .burger-button__line_3 {
  top: 51%;
  transform: rotate(-45deg) translateY(-50%);
}

.webp .hero {
  background: url("../img/hero-3.webp") center/cover, no-repeat;
}

.no-webp .hero {
  background: url("../img/hero-3.png") center/cover, no-repeat;
}

.webp .hero__content {
