  display: flex;
  justify-content: right;
  align-items: center;
  max-width: 1595px;
  padding: 0 20px;
  margin: 0 auto;
  width: 100%;
}
.hero__body {
  display: flex;
  align-items: center;
  position: relative;
  min-height: 530px;
}
.hero__left {
  margin-right: 20px;
  z-index: 2;
  padding: 90px 20px 85px 7%;
}
.hero__right {
  margin-top: -40px;
  z-index: 0;
  position: absolute;
  right: 0;
}
.hero .hero__content {
  border-radius: 60px;
}
.hero__title {
  margin-bottom: 30px;
  font-size: 52px;
  line-height: 115%;
  text-align: left;
  color: #fff;
  max-width: 750px;
}
@media (max-width: 1200px) {
  .hero__title {
    font-size: calc(32px + 20 * ((100vw - 320px) / 880));
  }
}
.hero__title .name {
  font-size: 52px;
  color: #5b7efb;
}
@media (max-width: 1200px) {
  .hero__title .name {
    font-size: calc(32px + 20 * ((100vw - 320px) / 880));
  }
}
.hero__text {
  margin: 0 0 40px;
  max-width: 600px;
  text-align: left;
  color: #fff;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  font-size: 20px;
}
@media (max-width: 1200px) {
  .hero__text {
    font-size: calc(17px + 3 * ((100vw - 320px) / 880));
  }
}
.hero__link_container {
  width: 100%;
}
.hero__links {
  display: flex;
  align-items: center;
}
.hero__link {
  user-select: none;
}
.hero__link_main {
  background-color: #5B7EFB;
  border-radius: 30px;
  z-index: 100;
  white-space: nowrap;
  border: 2px solid #fff;
  min-height: 60px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  transition: all 0.3s ease 0s;
}
.hero__link_main:hover span {
  background-color: #708EFC;
}
.hero__link_main:hover span::after {
  background-color: #708EFC;
}
.hero__link_main span {
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  position: relative;
  padding: 21px 25px 21px 50px;
  transition: all 0.3s ease 0s;
}
.hero__link_main span::after {
  position: absolute;
  right: -18px;
  top: 2px;
  content: "";
  display: block;
  width: 54px;
  height: 56px;
  background-color: #5B7EFB;
  transform: rotate(45deg);
  z-index: -1;
  transition: all 0.3s ease 0s;
}
.hero__link_main strong {
  background-color: #fff;
  display: block;
  font-weight: 700;
  font-size: 20px;
  line-height: 120%;
