.promo {
  margin-bottom: 60px;
  height: 100%;
}
.promo__container {
  max-width: 1920px;
  padding: 0 15px;
  margin: 0 auto;
  width: 100%;
}
.promo .promo-slider {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  padding: 100px 0;
}
.promo .promo-slider__image img {
  border-radius: 60px;
}
